.login-page {
  width: 100%;
  height: 100%;
  background: url(/static/media/bg.ef2f27ba.jpg) center / cover no-repeat;
}
.login-page header {
  height: 80px;
  background: rgba(182, 214, 255, 0.7);
  display: flex;
  align-items: center;
}
.login-page header img {
  width: 50px;
  height: 50px;
  margin: 0 20px 0 50px;
}
.login-page header h1 {
  font-size: 30px;
  font-weight: bold;
  color: white;
}
.login-page main .login-content {
  width: 400px;
  height: 350px;
  background: rgba(182, 214, 255, 0.7);
  padding: 20px 40px;
  margin: 100px auto;
}
.login-page main .login-content h1 {
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 50px;
}
.login-page main .login-content .login-form .login-form-button {
  width: 100%;
}
