.notfound {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  background: #f0f2f5;
}
.notfound .img {
  width: 500px;
  height: 500px;
  border: none;
  background: url(/static/media/404.9161e238.png) center / cover no-repeat;
}
.notfound .text {
  margin-left: 20px;
}
.notfound .text h2 {
  font-size: 30px;
}
.notfound .text p {
  font-size: 20px;
}
.notfound .text .backHome {
  margin: 0;
  padding: 0;
}
