.home {
  padding: 24px;
}
.home .topElement {
  display: flex;
}
.home .topElement .top-left {
  flex: 1 1;
}
.home .topElement .top-left .home-card {
  width: 250px;
  float: left;
}
.home .topElement .top-left .home-card .statis {
  font-weight: bold;
  font-size: 25px;
}
.home .topElement .top.rigth {
  flex: 3 1;
}
.home .topElement .top.rigth .lineChart {
  width: 100%;
}
.home .chartlayout {
  display: flex;
}
.home .chartlayout .barchart {
  float: left;
  width: 300px;
}
.home .chartlayout .timeline {
  float: right;
  width: 300px;
}
