.content-header {
  height: 80px;
  width: 100%;
}
.content-header .header-top {
  height: 40px;
  width: 100%;
  border-bottom: 1px solid green;
  text-align: right;
  padding-right: 30px;
  line-height: 40px;
}
.content-header .header-top #currentUser {
  font-weight: bold;
}
.content-header .header-top span {
  margin-left: 20px;
}
.content-header .header-bottom {
  display: flex;
  height: 40px;
  line-height: 40px;
}
.content-header .header-bottom .header-bottom-left {
  flex: 1 1;
  text-align: center;
  font-size: 20px;
  position: relative;
}
.content-header .header-bottom .header-bottom-left::after {
  content: '';
  position: absolute;
  top: 90%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  border-top: 20px solid white;
  border-bottom: 20px solid transparent;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
}
.content-header .header-bottom .header-bottom-right {
  flex: 3 1;
  text-align: right;
  padding-right: 30px;
}
.content-header .header-bottom .header-bottom-right img {
  width: 40px;
  height: 30px;
}
